<template>
  <v-app>
    <Nav :login="login" :logout="logout" :userProfile="userProfile" :loggedIn="loggedIn" />

    <v-main>
      <v-container fluid>
        <router-view></router-view>
      </v-container>
    </v-main>
    <Footer></Footer>
  </v-app>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Nav from "@/components/Nav";
import Footer from "@/components/Footer";

export default {
  name: "App",
  components: { Nav, Footer },
  computed: {
    ...mapGetters("user", {
      userProfile: "userProfile",
      loggedIn: "loggedIn"
    })
  },
  methods: {
    ...mapActions("user", {
      login: "login",
        logout: 'logout'
    })
  }
};
</script>
