<template>
  <v-app id="inspire">
    <v-main>
      <v-container class="fit-height" fluid>
        <v-row align="center" justify="center">
          <p>Welcome to this test project using Vue and Firbase Authentication</p>
        </v-row>
        <v-row align="center" justify="center">
          <div>
            <v-banner single-line v-if="!loggedIn">
              <v-icon slot="icon" color="red" size="36">mdi-alarm-light</v-icon>Unable to verify you as a real person! Please login!
            </v-banner>

            <v-banner v-else two-line>
              <v-avatar slot="icon" color="grey darken-3" size="40">
                <v-icon icon="mdi-lock" color="white">mdi-lock</v-icon>
              </v-avatar>
              {{userProfile.name}} has been logged in via Google Authentication
            </v-banner>
          </div>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>


    
  }
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'Home',
    computed: {
        ...mapGetters('user', {
        loggedIn: 'loggedIn',
        userProfile: 'userProfile'
      }),
    },
  props: {
    source: String
  },
  data: () => ({
    drawer: null
  }),
  created() {
    this.$vuetify.theme.dark = true;
  }
};
</script>